'use client';
import { useState, useEffect } from 'react';

export default function CommentSection() {
  const [comments, setComments] = useState([]);
  const [loading, setLoading] = useState(true);
  const [userName, setUserName] = useState('');
  const [userComment, setUserComment] = useState('');
  const [showSuccess, setShowSuccess] = useState(false);

  useEffect(() => {
    loadComments();
  }, []);

  const loadComments = async () => {
    try {
      const response = await fetch('https://jsonplaceholder.typicode.com/comments');
      const data = await response.json();
      setComments(data.slice(0, 5));
    } catch (error) {
      console.error('Error loading comments:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    
    const name = userName.trim() || 'Anonim';
    const comment = userComment.trim();
    
    if (!comment) {
      alert('Tulis sesuatu dulu ya!');
      return;
    }
    
    // Add user comment to top
    const newComment = {
      id: Date.now(),
      name,
      body: comment,
      isUserComment: true
    };
    
    setComments(prev => [newComment, ...prev]);
    setUserName('');
    setUserComment('');
    setShowSuccess(true);
    
    setTimeout(() => setShowSuccess(false), 2000);
  };

  const CommentItem = ({ comment }) => {
    const nameColor = comment.isUserComment ? 'text-pink-600' : 'text-gray-800';
    const avatarColor = comment.isUserComment ? 'bg-pink-500' : 'bg-gray-500';
    
    return (
      <div className="border-b border-gray-100 pb-3 mb-3">
        <div className="flex items-center mb-2">
          <div className={`w-6 h-6 ${avatarColor} rounded-full flex items-center justify-center text-white text-xs font-medium mr-2`}>
            {comment.name.charAt(0).toUpperCase()}
          </div>
          <h5 className={`font-medium ${nameColor}`}>
            {comment.name}
            {comment.isUserComment && <span className="text-xs text-pink-500 ml-2">(Baru)</span>}
          </h5>
        </div>
        <p className="text-gray-600 text-sm leading-relaxed ml-8">{comment.body}</p>
      </div>
    );
  };

  return (
    <div className="bg-white rounded-2xl shadow-sm border border-gray-100 p-6 mt-8">
      <h4 className="text-lg font-semibold text-gray-800 mb-4">Komentar</h4>
      
      {/* Comment Form */}
      <form onSubmit={handleSubmit} className="mb-6">
        <div className="mb-3">
          <input
            type="text"
            value={userName}
            onChange={(e) => setUserName(e.target.value)}
            placeholder="Nama (opsional)"
            className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-pink-500 focus:border-transparent text-sm"
          />
        </div>
        <div className="mb-3">
          <textarea
            value={userComment}
            onChange={(e) => setUserComment(e.target.value)}
            placeholder="Tulis komentar kamu..."
            rows={3}
            className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-pink-500 focus:border-transparent text-sm resize-none"
          />
        </div>
        <button
          type="submit"
          className="bg-pink-500 text-white px-4 py-2 rounded-lg hover:bg-pink-600 transition-colors text-sm font-medium"
        >
          Kirim Komentar
        </button>
      </form>
      
      {/* Comments List */}
      <div className="space-y-3">
        {loading ? (
          <p className="text-gray-500 text-center py-4">Memuat komentar...</p>
        ) : comments.length > 0 ? (
          comments.map(comment => (
            <CommentItem key={comment.id} comment={comment} />
          ))
        ) : (
          <p className="text-gray-500 text-center py-4">Belum ada komentar</p>
        )}
      </div>
      
      {/* Success Message */}
      {showSuccess && (
        <div className="fixed top-4 right-4 bg-green-500 text-white px-4 py-2 rounded text-sm z-50">
          Komentar berhasil dikirim!
        </div>
      )}
    </div>
  );
}
