import CustomButton from "../ui/CustomButton";

export default function CommentForm() {
  return (
    <div className="mb-8">
        <h3 className="text-xl font-semibold text-gray-800 mb-4">üí¨ Bagikan Pengalamanmu</h3>
        <p className="text-gray-600 mb-6">Ceritakan apa yang sudah kamu pelajari atau hal yang masih ingin kamu tahu</p>
        
        <form id="comment-form" className="space-y-4">
            <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">Nama (opsional)</label>
                <input type="text" id="user-name" placeholder="Tulis nama atau inisial kamu..." className="w-full px-3 py-2 border border-gray-300 rounded focus:border-pink-400 focus:outline-none" />
            </div>
            
            <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">Apa yang kamu pelajari hari ini?</label>
                <textarea id="user-comment" rows="3" placeholder="Bagikan pemikiran atau pertanyaan kamu..." className="w-full px-3 py-2 border border-gray-300 rounded focus:border-pink-400 focus:outline-none resize-none"></textarea>
            </div>
            
            <div className="flex justify-end">
                <CustomButton type="submit" title='Kirim' className="text-sm px-4 py-2" />
            </div>
        </form>
    </div>
  )
};
